@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700&subset=latin-ext");

html {
    height: 100%;
}

body {
    min-height: 100%;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 12px;
    font-weight: 400;
    margin: 0;
}

.container {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 100%;
    height: 100%;
}

.wrapper {
    position: relative;
    display: block;
    width: 100%;
    max-width: 500px;
    padding: 40px 0;
}
